.footer{
    display: flex;
    align-items: center;
    margin:-0.5rem -3.5rem;
    position: relative;
    margin-top: -7rem;

}
.f-content{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 4rem;
    gap: 2rem;
    color: white;

}
.f-icons{
    display: flex;
    gap: 2rem;
}

@media screen and (max-width: 480px){
    .f-content{
      transform: scale(.5);
    }
    span{
        font-size: 1rem;
    }
  }
