.works{
    padding:0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top:4rem;
}
.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
}
.awesome>:nth-child(1){
color: var(--black);
font-size: 2.5rem;
font-weight: bold;
}
.awesome>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
    }
.awesome>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
    }
.w-right{
    position: relative;
    right: 3rem;
}
.w-maincircle{
    position: relative;
    left: 9rem;
    height: 18rem;
    width: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}
.w-maincircle>*{
    position: absolute;
}
.w-maincircle>:nth-child(1){
    top: -3rem;
    left: 6rem;
}
.w-maincircle>:nth-child(2){
    left: -3rem;
    top: 5rem;
}
.w-maincircle>:nth-child(3){
    left: 6rem;
    top: 5rem;
}
.w-maincircle>:nth-child(4){
    left: 15rem;
    top: 5rem;
}
.w-maincircle>:nth-child(5){
    left: 6rem;
    top: 13rem;
}

.w-circle{
    border: 5px solid #ebebeb;
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    box-shadow: var(--smboxShadow);
    background: white;

}
.w-circle>img{
    transform: scale(0.6);
}
.bg-circle{
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}
.blue{
   background: #1949B8; 
   left: 18rem;
   top: 0rem;
}
.yellow{
    background: #F5C32C; 

    left: 18rem;
    top: 8rem;
 }
 @media screen and (max-width: 480px) {
    .works{
        flex-direction: column;
        height: 55rem;
        padding: 0;
        gap:5rem ;
    }
    .w-right{
        bottom: 8rem;
        display: flex;
        align-items: flex-start;
        justify-content: start;
        transform: scale(0.7);
        position: relative;

    }
    .w-maincircle{
        position: absolute;

    }
    .bg-circle{
        left: 8rem !important;
    }
 }
    