.service{
    padding: 0 3rem 0 3 rem;
    display: flex;
    height: 90vh;
    margin-bottom: 13rem;
    margin-top: 9rem;
}
.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
}
.awesome>:nth-child(1){
color: var(--black);
font-size: 2.5rem;
font-weight: bold;
}
.awesome>:nth-child(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
    }
.awesome>:nth-child(3){
    color: var(--gray);
    font-size: 14px;
    margin-top: 1rem;
    }
.s-button{
    width: 8rem;
    height: 2rem;
    margin-top:1rem ;
}
.cards{
    position: relative;   /* kisi container iv ki position relative krdo aur uske ander k sare div ki absolute to vo child div jis container ki pos relative hoti h uske aacording height widht length lete h*/
}
.cards>*{
    position: absolute;
}



.s-blur2{
    top: 13rem;
    left:-18rem;
}

@media screen and (max-width:480px){
    .service{
      margin-top: 0;
      flex-direction: column;
      gap:5rem;
      height: 66rem;
      padding: 0;
    }
    .cards{
       display: flex;
       flex-direction: column;
       gap: 17rem; 
    }
    .cards>*{
       position: static; 
    }
  }