.portfolio{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 70vh;
}
.portfolio span:nth-of-type(1){
    color: var(--black);
    font-size: 2rem;
    font-weight: bold;
}
.portfolio span:nth-of-type(2){
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.portfolio .swiper{
overflow: visible !important;
}
.portfolio-slider{
    margin-top: 3rem;
    width: 100%;
    

}
.portfolio-slider .swiper-slide{
    
    width: 20rem;
    

}

.portfolio img{
    width: 19rem;
    filter: drop-shadow(-12px 15px 13px rgba(0,0,0,0.25));
    border-radius: 19px;
}
.portfolio .swiper-pagination-bullet-active{
    background: var(--orange);
}
.t1,.t2{
font-size: 1rem;
margin-top: 2rem;
}